"use client";

import { useState } from "react";
import type { DashboardTemplate } from "@/stores/template-store";
import styles from "./use-template-dialog.module.css";

interface UseTemplateDialogProps {
  isOpen: boolean;
  template: DashboardTemplate | null;
  onClose: () => void;
  onSubmit: (projectName: string, inputs: PlaceholderInputs) => void;
}

interface PlaceholderInputs {
  [key: string]: string;
}

// Mock placeholders - will be extracted from template
const mockPlaceholders = [
  { id: "main-clip", name: "Main Video Clip", type: "media" as const },
  { id: "headline", name: "Headline Text", type: "text" as const },
  { id: "cta-text", name: "Call to Action", type: "text" as const },
];

export function UseTemplateDialog({
  isOpen,
  template,
  onClose,
  onSubmit,
}: UseTemplateDialogProps) {
  const [projectName, setProjectName] = useState("");
  const [inputs, setInputs] = useState<PlaceholderInputs>({});

  const handleInputChange = (placeholderId: string, value: string) => {
    setInputs((prev) => ({ ...prev, [placeholderId]: value }));
  };

  const handleSubmit = () => {
    if (!projectName.trim()) return;

    onSubmit(projectName.trim(), inputs);
    
    // Reset form
    setProjectName("");
    setInputs({});
    onClose();
  };

  if (!isOpen || !template) return null;

  return (
    <div className={styles.overlay} onClick={onClose}>
      <div className={styles.dialog} onClick={(e) => e.stopPropagation()}>
        <header className={styles.header}>
          <div>
            <h2>Use Template</h2>
            <p className={styles.templateName}>{template.name}</p>
          </div>
          <button className={styles.closeBtn} onClick={onClose}>
            ‚úï
          </button>
        </header>

        <div className={styles.content}>
          <div className={styles.formGroup}>
            <label htmlFor="projectName">Project Name</label>
            <input
              id="projectName"
              type="text"
              value={projectName}
              onChange={(e) => setProjectName(e.target.value)}
              placeholder="e.g., My New Video"
              autoFocus
            />
          </div>

          <div className={styles.placeholderSection}>
            <h3>Fill in Placeholders</h3>
            <p className={styles.placeholderHint}>
              Provide content for each placeholder in the template
            </p>

            <div className={styles.placeholderList}>
              {mockPlaceholders.map((placeholder) => (
                <div key={placeholder.id} className={styles.placeholderItem}>
                  <div className={styles.placeholderHeader}>
                    <span className={styles.placeholderIcon}>
                      {placeholder.type === "media" ? "üé¨" : "üìù"}
                    </span>
                    <span className={styles.placeholderName}>
                      {placeholder.name}
                    </span>
                    <span className={styles.placeholderType}>
                      {placeholder.type}
                    </span>
                  </div>
                  {placeholder.type === "text" ? (
                    <input
                      type="text"
                      value={inputs[placeholder.id] || ""}
                      onChange={(e) =>
                        handleInputChange(placeholder.id, e.target.value)
                      }
                      placeholder={`Enter ${placeholder.name.toLowerCase()}...`}
                    />
                  ) : (
                    <button className={styles.mediaBtn}>
                      <span>üìÅ</span>
                      <span>Select Media File</span>
                    </button>
                  )}
                </div>
              ))}
            </div>
          </div>
        </div>

        <footer className={styles.footer}>
          <button className={styles.cancelBtn} onClick={onClose}>
            Cancel
          </button>
          <button
            className={styles.generateBtn}
            onClick={handleSubmit}
            disabled={!projectName.trim()}
          >
            üöÄ Generate Project
          </button>
        </footer>
      </div>
    </div>
  );
}
