"use client";

import { useState } from "react";
import styles from "./create-template-dialog.module.css";

interface CreateTemplateDialogProps {
  isOpen: boolean;
  onClose: () => void;
  onSubmit: (template: NewTemplateData) => void;
}

export interface NewTemplateData {
  name: string;
  description: string;
  projectId?: string;
}

export function CreateTemplateDialog({
  isOpen,
  onClose,
  onSubmit,
}: CreateTemplateDialogProps) {
  const [step, setStep] = useState<"source" | "details">("source");
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [selectedProjectId, setSelectedProjectId] = useState<string | null>(null);

  // Mock recent OpenCut projects
  const recentProjects = [
    { id: "oc-1", name: "My YouTube Intro", duration: 15, updatedAt: "2 hours ago" },
    { id: "oc-2", name: "Product Demo", duration: 45, updatedAt: "Yesterday" },
    { id: "oc-3", name: "Social Media Clip", duration: 30, updatedAt: "3 days ago" },
  ];

  const handleSubmit = () => {
    if (!name.trim()) return;
    
    onSubmit({
      name: name.trim(),
      description: description.trim(),
      projectId: selectedProjectId || undefined,
    });
    
    // Reset form
    setStep("source");
    setName("");
    setDescription("");
    setSelectedProjectId(null);
    onClose();
  };

  const handleNext = () => {
    if (selectedProjectId) {
      setStep("details");
    }
  };

  const handleBack = () => {
    setStep("source");
  };

  if (!isOpen) return null;

  return (
    <div className={styles.overlay} onClick={onClose}>
      <div className={styles.dialog} onClick={(e) => e.stopPropagation()}>
        <header className={styles.header}>
          <h2>Create Template</h2>
          <button className={styles.closeBtn} onClick={onClose}>
            ‚úï
          </button>
        </header>

        {step === "source" && (
          <div className={styles.content}>
            <p className={styles.stepDescription}>
              Select an OpenCut project to use as the base for your template
            </p>

            <div className={styles.projectList}>
              {recentProjects.map((project) => (
                <button
                  key={project.id}
                  className={`${styles.projectCard} ${
                    selectedProjectId === project.id ? styles.selected : ""
                  }`}
                  onClick={() => setSelectedProjectId(project.id)}
                >
                  <div className={styles.projectIcon}>üé¨</div>
                  <div className={styles.projectInfo}>
                    <span className={styles.projectName}>{project.name}</span>
                    <span className={styles.projectMeta}>
                      {project.duration}s ‚Ä¢ {project.updatedAt}
                    </span>
                  </div>
                  {selectedProjectId === project.id && (
                    <span className={styles.checkmark}>‚úì</span>
                  )}
                </button>
              ))}
            </div>

            <div className={styles.divider}>
              <span>or</span>
            </div>

            <button className={styles.openEditorBtn}>
              <span>üöÄ</span>
              <span>Create New Project in OpenCut</span>
            </button>
          </div>
        )}

        {step === "details" && (
          <div className={styles.content}>
            <p className={styles.stepDescription}>
              Configure your template settings
            </p>

            <div className={styles.formGroup}>
              <label htmlFor="templateName">Template Name</label>
              <input
                id="templateName"
                type="text"
                value={name}
                onChange={(e) => setName(e.target.value)}
                placeholder="e.g., YouTube Shorts Template"
                autoFocus
              />
            </div>

            <div className={styles.formGroup}>
              <label htmlFor="templateDescription">Description</label>
              <textarea
                id="templateDescription"
                value={description}
                onChange={(e) => setDescription(e.target.value)}
                placeholder="Describe what this template is for..."
                rows={3}
              />
            </div>

            <div className={styles.previewSection}>
              <h4>Template Preview</h4>
              <div className={styles.previewCard}>
                <div className={styles.previewThumbnail}>üé¨</div>
                <div className={styles.previewDetails}>
                  <span className={styles.previewName}>
                    {name || "Untitled Template"}
                  </span>
                  <span className={styles.previewMeta}>
                    3 placeholders detected
                  </span>
                </div>
              </div>
            </div>
          </div>
        )}

        <footer className={styles.footer}>
          {step === "details" && (
            <button className={styles.backBtn} onClick={handleBack}>
              ‚Üê Back
            </button>
          )}
          <div className={styles.footerSpacer} />
          <button className={styles.cancelBtn} onClick={onClose}>
            Cancel
          </button>
          {step === "source" ? (
            <button
              className={styles.nextBtn}
              onClick={handleNext}
              disabled={!selectedProjectId}
            >
              Next ‚Üí
            </button>
          ) : (
            <button
              className={styles.createBtn}
              onClick={handleSubmit}
              disabled={!name.trim()}
            >
              Create Template
            </button>
          )}
        </footer>
      </div>
    </div>
  );
}
